

import Service from '../models/service.model'
import ServiceList from '../components/ServiceList.vue'
import ReviewList from '../components/ReviewList.vue'
import { Vue, Options } from 'vue-class-component'
import Client from '../models/client.model'
import Review from '../models/review.model'
import ServiceCarousel from '../components/ServiceCarousel.vue'
import MobileApplicationForm from '../components/MobileApplicationForm.vue'
import InformationBlock from '../components/InformationBlock.vue'
import Header from '../components/Header.vue'
import Footer from '../components/Footer.vue'
import Spinner from '../components/Spinner.vue'
import axios from 'axios'
import { API_URL } from '@/config'
// import api from '@/api'

@Options({
    components: {
        ServiceList,
        ReviewList,
        ServiceCarousel,
        MobileApplicationForm,
        InformationBlock,
        Header,
        Footer,
        Spinner
    }
})
export default class Home extends Vue {
    public services: Service[] = []

    public clients: Client[] = [
        {
            id: 1,
            name: "",
            photo: "client_1.jpg"
        },
        {
            id: 2,
            name: "",
            photo: "client_2.jpg"
        },
        {
            id: 3,
            name: "",
            photo: "client_3.jpg"
        },
    ]

    public reviews: Review[] = [
        {
            id: 1,
            client: this.clients[0],
            text: "  .   . ",
            date: new Date(),
            assessment: 4
        },
        {
            id: 2,
            client: this.clients[1],
            text: ",     .     .",
            date: new Date(),
            assessment: 5
        },
        {
            id: 2,
            client: this.clients[2],
            text: "   .   ",
            date: new Date(),
            assessment: 5
        }
    ]

    async beforeMount() {
       let response = await axios.get(`${API_URL}/services`)
       console.log(response.data)
       let list: Service[] = []
       for (let i = 0; i < 4; i++) {
           const service = new Service()
           debugger
           let element = response.data[i]
           service.id = element.id
           service.name = element.name
           service.description = element.description
           service.details = [
               ...(element.details)
           ]
           service.price  = element.price
           list.push(service)
       }
       this.services.push(...list)
    }
}

